import Vue from "vue";
import App from "./App.vue";
import {
  Button,
  Select,
  Container,
  Header,
  Aside,
  Main,
  Submenu,
  MenuItem,
  Menu,
  MenuItemGroup,
  Dropdown,
  DropdownMenu,
  DropdownItem,
  Row,
  Col,
  Card,
  Table,
  TableColumn,
  Breadcrumb,
  BreadcrumbItem,
  Tag,
  Form,
  FormItem,
  Input,
  DatePicker,
  Option,
  Switch,
  Dialog,
  Pagination,
  MessageBox,
  Message,
} from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import VueRouter from "vue-router";
import router from "../router";
import Vuex from "vuex";
import store from "../store";
import http from "axios";
import "../api/mock.js";

Vue.use(Button);
Vue.use(Select);
Vue.use(VueRouter);
Vue.use(Container);
Vue.use(Header);
Vue.use(Aside);
Vue.use(Main);
Vue.use(MenuItem);
Vue.use(Submenu);
Vue.use(Menu);
Vue.use(MenuItemGroup);
Vue.use(Dropdown);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Vuex);
Vue.use(Row);
Vue.use(Col);
Vue.use(Card);
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(Breadcrumb);
Vue.use(BreadcrumbItem);
Vue.use(Tag);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Input);
Vue.use(DatePicker);
Vue.use(Option);
Vue.use(Switch);
Vue.use(Dialog);
Vue.use(Pagination);

Vue.config.productionTip = false;
Vue.prototype.$http = http;
Vue.prototype.$confirm = MessageBox.confirm;
Vue.prototype.$message = Message;

//路由守卫：判断当前页是否为登录页并且有无token
router.beforeEach((to, from, next) => {
  //防止页面刷新后，vuex丢失token信息
  store.commit("getToken");
  const token = store.state.User.token;
  if (!token && to.name !== "login") {
    next({ name: "login" });
  } else {
    next();
  }
});

new Vue({
  store,
  router,
  render: (h) => h(App),
  created() {
    store.commit("addMenu", router);
  },
}).$mount("#app");
