import Cookie from "js-cookie";
export default {
  state: {
    isCollapse: false,
    tabList: [
      {
        path: "/home",
        name: "home",
        label: "首页",
        icon: "s-home",
        url: "Home/Home",
      },
    ],
    currentMenu: null,
    menu: [],
  },
  mutations: {
    //侧边栏折叠
    collapseMenu(state) {
      state.isCollapse = !state.isCollapse;
    },
    //点击侧边栏时，增加头部面包屑数据
    selectMenu(state, val) {
      if (val.name !== "home") {
        state.currentMenu = val;
        const result = state.tabList.findIndex(
          (item) => item.name === val.name
        );
        if (result == -1) {
          state.tabList.push(val);
        } else {
          state.currentMenu = null;
        }
        Cookie.set("tabList", JSON.stringify(state.tabList));
      }
    },
    //获取当前面包屑数据
    getTag(state) {
      //因为Cookie中的tabList只在selectMenu时才被添加,但在页面加载时触发getTag函数获取tabList，
      // 此时Cookie.get(tabList)为undefined,而JSON.parse(undefined)会报错，所以要提前判断
      if (Cookie.get("tabList")) {
        state.tabList = JSON.parse(Cookie.get("tabList"));
      } else {
        state.tabList=state.tabList;
      }
    },
    //删除当前所点击的tag的标签数据
    closeTag(state, val) {
      const index = state.tabList.findIndex((item) => val.name === item.name);
      state.tabList.splice(index, 1);
      //删除之后更新Cookie中的tabList值
      Cookie.set("tabList",JSON.stringify(state.tabList));
    },
    //动态添加menu相关方法
    setMenu(state, val) {
      state.menu = val;
      Cookie.set("menu", JSON.stringify(val));
    },
    clearMenu(state) {
      state.menu = [];
      Cookie.remove("menu");
    },
    addMenu(state, router) {
      if (!Cookie.get("menu")) {
        return;
      }
      const menu = JSON.parse(Cookie.get("menu"));
      state.menu = menu;
      const menuArray = [];
      menu.forEach((item) => {
        //判断是否有子菜单
        if (item.children) {
          item.children = item.children.map((item) => {
            item.component = () => import(`../views/${item.url}`);
            return item;
          });
          menuArray.push(...item.children);
        } else {
          item.component = () => import(`../views/${item.url}`);
          menuArray.push(item);
        }
      });
      //动态添加路由
      menuArray.forEach((item) => {
        router.addRoute("Main", item);
      });
    },
  },
};
