//mock模拟后端登录接口权限
import Mock from "mockjs";
export default {
  getMenu: (config) => {
    const { username, password } = JSON.parse(config.body);
    //判断用户和密码
    if (username === "admin" && password === "admin") {
      return {
        code: 20000,
        data: {
          menu: [
            {
              path: "/home",
              name: "home",
              label: "首页",
              icon: "s-home",
              url: "home/index",
            },
            {
              path: "/mall",
              name: "mall",
              label: "商品管理",
              icon: "video-play",
              url: "mall/index",
            },
            {
              path: "/user",
              name: "user",
              label: "用户管理",
              icon: "user",
              url: "user/index",
            },
            {
              label: "其它",
              icon: "location",
              children: [
                {
                  path: "/pageOne",
                  name: "pageOne",
                  label: "页面1",
                  icon: "setting",
                  url: "others/pageOne",
                },
                {
                  path: "/pageTwo",
                  name: "pageTwo",
                  label: "页面2",
                  icon: "setting",
                  url: "others/pageTwo",
                },
              ],
            },
          ],
          token: Mock.Random.guid(),
          message: "获取成功",
        },
      };
    } else if (username === "zhangsan" && password === "zhangsan") {
      return {
        code: 20000,
        data: {
          menu: [
            {
              path: "/home",
              name: "home",
              label: "首页",
              icon: "s-home",
              url: "home/index",
            },
            {
              path: "/mall",
              name: "mall",
              label: "商品管理",
              icon: "video-play",
              url: "mall/index",
            },
          ],
          token: Mock.Random.guid(),
          message: "获取成功",
        },
      };
    } else {
      return {
        code: -999,
        data: {
          message: "用户密码错误",
        },
      };
    }
  },
};
