<template>
  <header>
    <div class="l-content">
      <el-button
        @click="handleMenu"
        plain
        icon="el-icon-menu"
        size="mini"
      ></el-button>
      <el-breadcrumb class="myColor" separator="/">
        <el-breadcrumb-item
          v-for="item in tags"
          :key="item.path"
          :to="{ path: item.path }"
          >{{ item.label }}</el-breadcrumb-item
        >
      </el-breadcrumb>
    </div>
    <div class="r-content">
      <el-dropdown trigger="click" size="mini">
        <span><img :src="src" /></span>
        <el-dropdown-menu>
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item @click.native="logout">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </header>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "CommonHeader",
  data() {
    return {
      src: require("../assets/09.jpg"),
    };
  },
  methods: {
    handleMenu() {
      this.$store.commit("collapseMenu");
    },
    logout() {
      this.$store.commit("clearToken");
      this.$store.commit("clearMenu");
      this.$router.push("/login");
    },
  },
  computed: {
    ...mapState({
      tags: (state) => state.Tab.tabList,
    }),
  },
};
</script>

<style lang="less" scoped>
header {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-content: center;
}
.l-content {
  display: flex;
  align-items: center;
  el-button {
    display: inline-block;
  }
  h3 {
    display: inline-block;
    color: #fff;
    margin-left: 15px;
    font-size: 14px;
    font-weight: 400;
  }
  .el-breadcrumb {
    margin-left: 20px;
  }
  //修改面包屑颜色和cursor
  // [@vue/compiler-sfc] the >>> and /deep/ combinators have been deprecated. Use :deep() instead.
  // /deep/组合标签被弃用，用deep()方法替代
  // .myColor/deep/.el-breadcrumb__inner {
  .myColor:deep(.el-breadcrumb__inner) {
    color: white;
    cursor: pointer;
  }
}
.r-content {
  display: flex;
  align-items: center;
  img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
}
</style>
