import Cookie from "js-cookie";
//借助js-cookie来缓存后端返回的token
export default {
  state: {
    token: "",
    user: "",
  },
  mutations: {
    setToken(state, val) {
      state.token = val;
      Cookie.set("token", val);
    },
    clearToken(state, val) {
      state.token = "";
      Cookie.remove("token");
    },
    getToken(state) {
      state.token = state.token || Cookie.get("token");
    },
    //设置用户
    setUser(state, val) {
      val = val.slice(0, 1).toUpperCase() + val.slice(1);
      state.user = val;
      Cookie.set("user", val);
    },
    //获取用户
    getUser(state) {
      state.user = state.user || Cookie.get("user");
    },
  },
};
